import React from 'react';
import '../styles/styles.css';
import {AdvancedImage, lazyload, placeholder} from '@cloudinary/react';
import {CloudinaryImage, URLConfig, CloudConfig} from "@cloudinary/url-gen";

export default class ClickableImage extends React.Component {
    render() {
        return (
            <>
            <div className="d-card-img">
                <AdvancedImage className="d-card-img"
                id={`${this.props.folder}_${this.props.imgName}`}
                cldImg={ new CloudinaryImage(`${this.props.folder}/${this.props.imgName}`, 
                    new CloudConfig({cloudName: 'lorenzkleiser'}), new URLConfig({secure: true}))}
                onClick={() => this.props.onImgClick(`https://res.cloudinary.com/lorenzkleiser/image/upload/${this.props.folder}/${this.props.imgName}`)}
                plugins={[lazyload(), placeholder({mode: 'predominant-color'})]}
                />
            </div>
          </> 
        );
    }
};